.container{
    background-color: #2F2E41;
    height: 75vh;
    width: 100%;
    display: flex;
   justify-content: space-evenly;
   align-items: center;
  
}



.left{
    width: 40%;
    height: 80%;
   position: relative;
   display: flex;
   justify-content: center;
   align-items: center;
  
}



.left .pic1{
    height: 75%;
    width: 50%;
    object-fit: cover;
    border-radius: 10px;
    transform: rotateZ(10deg);
    cursor: pointer;
    position: absolute;
    z-index: 10;
    top: 25%;
   
    
   
}

.left .backImg{
    height: 75%;
    width: 50%;
    background-color: rgba(255, 255, 255, 0.269);
    border-radius: 10px;
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    position: absolute;
    top: 25%;
    z-index:9 ;
   

}
.left .pic2{
    height: 75%;
    width: 50%;
    object-fit: cover;
    border-radius: 10px;
    cursor: pointer;
    position: absolute;
    z-index: 8;
    top: 25%;
   
}



.left .pic1:hover{
    transform: rotateZ(0deg);
}



.box{
    height: 80%;
    width: 60%;
  
    
}

.box1{
    height: 30%;
    width: 90%;
    text-align: center;
    color: white;
    font-weight: bold;
    display: flex;
    justify-content: center;
    
    margin-left: 5%;
    
   
    
    
    

}

.box1 span{
    font-size: 120%;

}

.box2{
    width: 100%;
    height: 70%;
    display: flex;
    justify-content: space-around;
   
    
}


.flipCard{
    height: 100%;
    width: 28%;
    background-color: transparent;
    perspective: 1000px;
    cursor: pointer;

}

.flipCardInner{
        position:relative;
        height: 100%;
        width: 100%;
        transition: transform 0.8s;
        transform-style: preserve-3d;
}

.flipCard:hover .flipCardInner{
    transform: rotateY(180deg);
}

.frontEnd , .flipCardback{
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
}


.box2 .frontEnd{
    height: 100%;
    width: 100%;
    background-color:purple;
    display: block;

}



.desktop{
    width: 20%;
    height: 20%;
    color: white;
    padding-left: 5%;
    margin-top: 60%;
}

.frontEnd .text{
    width: 60%;
    padding-left: 5%;
    color: rgb(255, 255, 255);
    font-size: 1.3rem;
    font-weight: bold;
    
}

.flipCardback{
    background-color: rebeccapurple;
    color: aliceblue;
    transform: rotateY(180deg);
    
    
    
}

.flipCardback h3,
ul{
    margin-left:10% ;

}



.flipCardback ul li{
    font-weight: bold;
    font-size: 1.3rem;
}

.flipCardback h4{
    font-size: 1.3rem;

}

.flipCardback h4 span{
    color:rgb(190, 123, 0) ;
    margin-left: 5%;
    font-size: 1.3rem;
   
}



.mobileApp , .flipCardback{
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
}

.mobileApp{
    height: 100%;
    width: 100%;
    background-color:rgba(0, 193, 193, 0.628);
    display: block;
    backdrop-filter: blur(50px);
    -webkit-backdrop-filter: blur(50px);

}

.mobileApp .mobile{
    width: 20%;
    height: 20%;
    color: white;
    padding-left: 5%;
    margin-top: 60%;
}

.mobileApp .text{
    width: 60%;
    padding-left: 5%;
    color: white;
    font-size: 1.3rem;
    font-weight: bold;

}


.Algo , .flipCardback{
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
}

.Algo{
    height: 100%;
    width: 100%;
    background-color:black;
    display: block;
    backdrop-filter: blur(50px);
    -webkit-backdrop-filter: blur(50px);

}

.Algo .algo{
    width: 20%;
    height: 20%;
    color: lightskyblue;
    padding-left: 5%;
    margin-top: 60%;

}

.Algo .text{
    width: 90%;
    padding-left: 5%;
    color: white;
    font-size: 1.2rem;
    font-weight: bold;

}

@media (max-width:480px){
    .container{
        flex-direction: column;
        height:auto;
        
       
        
    }

    .left{
        width: 100%;
        height: 30vh;
        position: relative;
        margin-top: 140vh;
    }

    .left .pic1{
        height: 85%;
        width: 80%;
        top: 8%;
        transform: rotate(0deg);
    }

    .left .backImg{
        height: 85%;
        width: 80%;
        top: 8%;
    }

    .left .pic2{
        width: 80%;
        height: 85%;
        top: 8%;
    }

    

    .box{
        width: 95%;
        height: auto;

       
        
    }

    .box1{
       
        height: 20vh;
        align-items: center;
        
    }

    .box2{
        flex-direction: column;
        align-items: center;
        height: auto;
        width: 100%;
        
        
    }

    .flipCard{
        width: 80%;
        height:45vh;
        padding-bottom:12%;
    }

    .Algo .algo{
        margin-top: 75%;
    }
    

   



   
}


